import {
  Avatar,
  Box,
  Typography,
  Card,
  CardContent,
  Chip,
  Button,
} from "@mui/joy";
import { Form, Formik } from "formik";
import PromotionSchema from "../../validation-schemas/product/PromotionSchema";
import resolvePhotoSrc from "../../utils/resolve-photo-src";
